<template>
  <v-app id="inspire">
    <v-content>
      <v-container fill-height fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-card class="elevation-12">
              <v-toolbar dark flat>
                <v-spacer></v-spacer>
                <v-toolbar-title class="headline">Login</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field single-line outlined dense class="title" label="Login" name="login" prepend-icon="mdi-account" type="text" v-model="username" />

                  <v-text-field single-line outlined dense class="title" id="password" v-on:keyup.enter="login" label="Password" name="password" prepend-icon="mdi-lock-alert" type="password"
                    v-model="password" />
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn @click="login" class="subtitle-1 grey darken-3 white--text" block>Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
      <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="6000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-content>
  </v-app>
</template>

<script>
  export default {
    data: () => ({
      valid: false,
      password: '',
      nameRules: [
        v => !!v || 'Masukkan Username',
      ],
      username: '',
      source: String,
      snackbar: false,
      color: null,
      text: '',
    }),
    methods: {
      login: function () {
        let username = this.username
        let password = this.password
        this.$store.dispatch('login', {
            username,
            password
          })
          .then(() => this.$router.push({
            name: 'Secure'
          }))
          .catch(err => {
            console.log(err)
            this.snackbar = true;
            this.text = 'Data salah atau Tidak Aktif';
            this.color = 'red';
          })
      }
    }
  }
</script>
<style>
  #inspire{
    /* background-color: aqua; */
    background-image: url('../../assets/background-login.jpg');
  }
</style>